<template>
    <div class="m-l-10">
        <div class="m-t-10 ">
            <p class="header-line"><i class="fa fa-arrow-left m-r-10 pointer c-primary" aria-hidden="true" @click="back()"></i>{{$t('workorder.workorderItem')}}</p>
        </div>
        <!-- <el-col :span="5">
            <div class="wd-intro">
                <ul>
                    <li>
                        <span>{{$t('label.workorderName')}}</span><i>{{wdname}}</i>
                    </li>
                    <li class="wd-content">
                        <ul>
                            <li>{{$t('workorder.introContent1')}} </li>
                            <li>{{$t('workorder.introContent2')}} </li>
                            <li>{{$t('workorder.introContent3')}} </li>
                            <li>{{$t('workorder.introContent4')}} </li>
                            <li>{{$t('workorder.introContent5')}} </li>
                        </ul>
                    </li>
                </ul>
            </div>
        </el-col> -->
        <div class="m-t-20" v-if="wdtype == groupType.android">
           <android-details
           :wdtype="wdtype"
           :wdname="wdname">
           </android-details>
        </div>
        <div class="m-t-20" v-if="wdtype == groupType.linux">
           <linux-details
           :wdtype="wdtype"
           :wdname="wdname">
           </linux-details>
        </div>
        <div class="m-t-20" v-if="wdtype == groupType.windows">
           <windows-details
           :wdtype="wdtype"
           :wdname="wdname">
           </windows-details>
        </div>
    </div>
   
</template>
<script>
    import androidDetails from "./details/androidDetails"
    import linuxDetails from "./details/linuxDetails"
    import windowsDetails from "./details/windowsDetails"
    import {groupType} from "../../assets/js/constant"


    export default{
        name: 'workerorder-details',
        data(){
            return {
                wdname: "",
                listLoading: false,
                wdtype: "",
                groupType
            }
        },

        components: { 
            linuxDetails,
            androidDetails,
            windowsDetails
        },

        methods:{
            //batch selected
            selectItem(val) {
                this.multipleSelection = val;
            },

            back(){
                this.$router.go(-1);
            },

        },
        created() {
            if(this.$route.query.wdname){
                this.wdname = this.$route.query.wdname;
                this.wdtype = this.$route.query.type;
            }
        },

    }
</script>

<style lang="scss" scoped>
@import "../../assets/css/colors";

    $box-width: 170px;
    $box-height: 200px;
    .wd-intro{
        li{
            &:first-child{
                margin-top: 15px;
                i{
                    display: inline-block;
                    margin-left: 10px;
                    padding: 5px 10px;
                    border-bottom: 1px solid #000;
                }
            }

            &:nth-child(2){
                p{
                    &:first-child{
                        line-height: 26px;
                        font-size: 16px;
                        color: #000;
                        margin-bottom: 5px;
                    }
                    &:nth-child(2){
                        line-height: 26px;
                        font-size: 14px;
                        color: rgba(25,0,29,.5);
                        margin-bottom: 10px;
                    }
                }
               
                
            }
            padding: 10px;
            
        }
        margin-top: 20px;
        margin-right: 15px;
        padding: 10px;
        min-height: 418px;
        border: 1px solid #eee;
        border-top: 3px solid $warn-color;
        border-radius: 5px;
        -moz-box-shadow: 0 1px 12px 0 rgba(0,0,0,0.1);
        -webkit-box-shadow: 0 1px 12px 0 rgba(0,0,0,0.1);
        box-shadow: 0 1px 12px 0 rgba(0,0,0,0.1);
        &:hover{
            -moz-box-shadow: 0 4px 12px 0 rgba(0,0,0,0.3);
            -webkit-box-shadow: 0 4px 12px 0 rgba(0,0,0,0.3);
            box-shadow: 0 4px 12px 0 rgba(0,0,0,0.3);
            -webkit-transition: all 0.3s;
            -moz-transition: all 0.3s;
            -ms-transition: all 0.3s;
            -o-transition: all 0.3s;
            transition: all 0.3s;
        }
    }
    .noData{
        margin-top: 30px;
        p{
            img{
                width: 27px;
                vertical-align: middle;
            }
            margin-bottom: 20px;
            color: #bfbfbf;
            font-size: 21px;
        }
    }
    .deployBtn{
        margin:25px 10px;
    }

    .ov-box{
        .ov-header{
            border-left: 4px solid #108cee;
            font-size: 21px;
            margin: 20px;
            padding-left: 15px;
            color: #141a24;
        }
        .ov-content{
            
            .card-header{
                p{
                    &:first-child{
                        float: left;
                    }
                    &:nth-child(2){
                        float: right;
                        cursor: pointer;
                    }
                }
                background: #ffffee
            }
            margin-left: 10px;
            margin-bottom: 20px;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
        }
        display: inline-block;
        margin-left: 20px;
    }
    .wd-icon{
        display: inline-block;
        position: relative;
        top:3px;
    }
    .action-icon{
        display: inline-block;
        cursor: pointer;
        position: relative;
        top:3px;
    }
    
</style>



