<template>
    <div>
        <el-tabs v-model="activeName">
            <el-tab-pane name="exePkg">
                <span slot="label">
                    <i class="fa fa-windows m-r-5"></i>
                    {{$t('workorder.exePkg')}}
                </span>
                <el-table
                    :data="exeTableData"
                    tooltip-effect="dark"
                    style="width: 100%"
                    v-loading="listLoading">

                    <el-table-column
                    label=""
                    width="100"
                    >
                        <template slot-scope="scope">
                            <div>
                                <img :src="getSrc(scope.row.reid)" :onerror="defaultImg" alt="no img" style="width:52px"/>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="projectname"
                    :label="$t('winPkgControl.projectName')"
                    min-width="120"
                    sortable
                    >
                    </el-table-column>
                    <el-table-column
                    prop="version"
                    :label="$t('winPkgControl.version')"
                    min-width="120"
                    sortable>
                    </el-table-column>

                    <el-table-column
                    :label="$t('global.action')"
                    min-width="120"
                    >
                        <template slot-scope="scope">
                            <div class="action-icon">
                                <svg t="1572233483828" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" 
                                    p-id="10629" width="20" height="20" 
                                    v-if="!scope.row.isSave" 
                                    @click="addFile(scope.row, 'installExe')">
                                    <path d="M831.55 128.531c38.35 0 63.911 25.568 63.911 63.91v639.104c0 38.355-25.561 63.916-63.91 63.916H192.44c-38.34 0-63.908-25.56-63.908-63.916V192.442c0-38.343 25.567-63.91 63.908-63.91h639.11m0-63.91H192.44c-70.3 0-127.816 57.518-127.816 127.82v639.103c0 70.308 57.515 127.833 127.816 127.833h639.11c70.294 0 127.822-57.525 127.822-127.833V192.442c0-70.302-57.527-127.82-127.823-127.82z m0 0" p-id="10630" fill="#aaaaaa"></path>
                                </svg>
                                <svg t="1572233566165" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
                                    p-id="10804" width="20" height="20"
                                    v-else
                                    @click="deleteFile(scope.row.wiid, 'installExe')">
                                    <path d="M744.512 392.768L453.825 681.984l-41.92-41.664 290.689-289.28 41.92 41.728z m-423.168 73.984l132.481 131.84-41.92 41.728-132.481-131.904 41.92-41.664z m527.872 479.872H174.785c-62.016 0-112.384-48.704-112.384-108.671V186.05c0-59.968 50.432-108.671 112.384-108.671h674.431c62.016 0 112.384 48.704 112.384 108.671v651.903c0 59.968-50.368 108.671-112.384 108.671z m56.192-760.576c0-29.952-25.217-54.336-56.193-54.336H174.784c-30.976 0-56.193 24.384-56.193 54.336v651.903c0 29.952 25.217 54.336 56.193 54.336h674.431c30.976 0 56.193-24.384 56.193-54.336V186.048z" p-id="10805" fill="#3c8dbc"></path>
                                </svg>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="deployBtn" v-show="exeBtnIsShow">
                    <el-button 
                    size="small" 
                    type="default" 
                    @click="back()" 
                    >
                        {{$t('global.back')}}
                    </el-button>
                    <el-button 
                    size="small" 
                    type="primary" 
                    @click="goSummary()" 
                    >
                        {{$t('global.summary')}}
                    </el-button>
                </div>
            </el-tab-pane>
            <el-tab-pane name="compressed">
                <span slot="label">

                    <i class="fa fa-file-archive-o m-r-5" ></i>
                    {{$t('workorder.compressedPkg')}}
                </span>
                <el-table
                    :data="pkgTableData"
                    tooltip-effect="dark"
                    style="width: 100%"
                    v-loading="listLoading"
                    :header-cell-style="{background:'#fafafa',color:'#606266'}"
                    >

                    <el-table-column
                    label=""
                    width="100"
                    >
                        <template slot-scope="scope">
                            <div>
                                <img src="@/assets/imgs/package.png"  alt="no img" style="width:48px" />
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="filename"
                    :label="$t('linuxPkgControl.fileName')"
                    min-width="120"
                    >
                    </el-table-column>
                    <el-table-column
                    prop="projectname"
                    :label="$t('winPkgControl.projectName')"
                    min-width="120"
                    sortable
                    >
                    </el-table-column>
                    <el-table-column
                    prop="version"
                    :label="$t('winPkgControl.version')"
                    min-width="120"
                    sortable>
                    </el-table-column>
                    <el-table-column
                    :label="$t('global.action')"
                    min-width="120"
                    >
                        <template slot-scope="scope">
                            <div class="action-icon">
                                <svg t="1572233483828" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" 
                                    p-id="10629" width="20" height="20" 
                                    v-if="!scope.row.isSave" 
                                    @click="addFile(scope.row, 'installZip')">
                                    <path d="M831.55 128.531c38.35 0 63.911 25.568 63.911 63.91v639.104c0 38.355-25.561 63.916-63.91 63.916H192.44c-38.34 0-63.908-25.56-63.908-63.916V192.442c0-38.343 25.567-63.91 63.908-63.91h639.11m0-63.91H192.44c-70.3 0-127.816 57.518-127.816 127.82v639.103c0 70.308 57.515 127.833 127.816 127.833h639.11c70.294 0 127.822-57.525 127.822-127.833V192.442c0-70.302-57.527-127.82-127.823-127.82z m0 0" p-id="10630" fill="#aaaaaa"></path>
                                </svg>
                                <svg t="1572233566165" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
                                    p-id="10804" width="20" height="20"
                                    v-else
                                    @click="deleteFile(scope.row.wiid, 'installZip')">
                                    <path d="M744.512 392.768L453.825 681.984l-41.92-41.664 290.689-289.28 41.92 41.728z m-423.168 73.984l132.481 131.84-41.92 41.728-132.481-131.904 41.92-41.664z m527.872 479.872H174.785c-62.016 0-112.384-48.704-112.384-108.671V186.05c0-59.968 50.432-108.671 112.384-108.671h674.431c62.016 0 112.384 48.704 112.384 108.671v651.903c0 59.968-50.368 108.671-112.384 108.671z m56.192-760.576c0-29.952-25.217-54.336-56.193-54.336H174.784c-30.976 0-56.193 24.384-56.193 54.336v651.903c0 29.952 25.217 54.336 56.193 54.336h674.431c30.976 0 56.193-24.384 56.193-54.336V186.048z" p-id="10805" fill="#3c8dbc"></path>
                                </svg>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="deployBtn"  v-show="compressedBtnIsShow">
                    <el-button 
                    size="small" 
                    type="default" 
                    @click="back()" 
                    >
                        {{$t('global.back')}}
                    </el-button>
                    <el-button 
                    size="small" 
                    type="primary" 
                    @click="goSummary()" 
                    >
                        {{$t('global.summary')}}
                    </el-button>
                </div>
            </el-tab-pane>
        </el-tabs> 
    </div>
</template>
<script>
    import {workorder} from '@/assets/js/lwm2mMap';
    import handleResponse from '@/components/restfulapi/handleResponse';
    import {getRepoWinPkgsApi, getRepoWinPkgsByTypeApi} from '../../../components/restfulapi/getRepoWinPackageApi';
    
    import {
        getWorkorderApi,
        getWorkorderByNameApi,
        getWorkorderItemByNameApi,

        editWorkorderApi,
        deleteWorkorderApi,
        addWorkorderApi,
        addWorkorderItemApi,
        deleteWorkorderItemApi,

    } from '@/components/restfulapi/workorderApi';
    import {checkSession, getSession, setSession } from '@/assets/js/storage';
    import {returnDownloadServerUrlApi, getDownloadServerUrlApi} from "@/components/restfulapi/serverConfigApi"
    import {singleWinPkgType, batchWinPkgType, winPkgExe, winPkgZip } from "../../../assets/js/constant";
    export default{
        name: 'workerorder-add',
        data(){
            let validateDepName = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[\u4E00-\u9FA5A-Za-z0-9_\-\.\s]{1,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            return {
                activeName: "exePkg",
                pkgTableData: [],
                exeTableData: [],
                defaultImg: 'this.src="' + 'static/imgs/default_windows.png' + '"',
                listLoading: false,
            }
        },

        props:{
            wdname: {
                type: String,
                default: ""
            }, 

            wdtype: {
                type: String,
                default: ""
            }, 
        },

        methods:{
            //batch selected
            selectItem(val) {
                this.multipleSelection = val;
            },
            
            getRepoPkgs(type){
                this.listLoading = true;
                getRepoWinPkgsByTypeApi(type).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                this.winPkgAssignWdItems(res.data, type);
                            }
                            
                        }
                    })
                })
            },

            winPkgAssignWdItems(repoPkgs, type){
                getWorkorderItemByNameApi(this.wdname).then((data) => {
                    handleResponse(data, (res) => {
                        if(Object.prototype.toString.call(res) === "[object Array]"){
                            let pkgData = [];
                            repoPkgs.forEach((fileInfo) => {
                                res.forEach((item) => {
                                    if(item.pkgname === fileInfo.projectname&&
                                    item.versionname === fileInfo.version&&
                                    (item.target === workorder.installExe|| item.target === workorder.installZip)){
                                        fileInfo.isSave = true;
                                        fileInfo.wiid = item.wiid;
                                    }
                                })
                                pkgData.push(fileInfo);
                            })
                            if(type == winPkgExe){
                                this.exeTableData = pkgData;
                            }else{
                                this.pkgTableData = pkgData;
                            }  
                        }
                    })
                })
            },
            
            addFile(fileinfo, type){
                let pkgname = fileinfo.projectname;
                let versionname = fileinfo.version;
                let address = fileinfo.address;
                let tool = fileinfo.tool
                this.listLoading = true;
                addWorkorderItemApi(this.wdname, fileinfo.filename, workorder[type], pkgname, versionname, address, tool).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        if(res.status === "CHANGED"){
                            if(type === "installExe"){
                                this.getRepoPkgs(winPkgExe);
                            }else{
                                this.getRepoPkgs(winPkgZip);
                            }
                            
                        }else{
                            _g.handleError(res);
                        }
                    })
                })
            },

            
            deleteFile(wiid, type) {
                this.listLoading = true;
                deleteWorkorderItemApi(wiid).then((res) => {
                    this.listLoading = false;
                    handleResponse(res, (data) => {
                        if(data.status === "CHANGED"){
                            if(type === "installExe"){
                                this.getRepoPkgs(winPkgExe);
                            }else{
                                this.getRepoPkgs(winPkgZip);
                            }   
                        }else{
                            _g.handleError(data);
                        }
                    })
                })
            },
            

            initData(){
                this.pkgTableData = [];
                this.exeTableData = [];
            },

            goSummary(){
                this.$router.push({path: "/main/workorder/summary", query: { wdname: this.wdname, type: this.wdtype}})
            },

            getSrc(reid){
                if(reid){
                    let repourl =  getRepoSrcApi();
                    return repourl+"/getExeIcon/"+reid;
                }else{
                    return 'static/imgs/default_windows.png';
                }
            },

            back(){
                this.$router.go(-1);
            },
        },
        
        computed: {
            exeBtnIsShow(){
                if(this.exeTableData.length>0){
                    return true;
                }else{
                    return false;
                }
            },

            compressedBtnIsShow(){
                if(this.pkgTableData.length>0){
                    return true;
                }else{
                    return false;
                }
            },
        },

        created() {
            this.getRepoPkgs(winPkgExe);
            this.getRepoPkgs(winPkgZip);
        },

    }
</script>
<style lang="scss" scoped>
@import "../../../assets/css/colors";
    .wd-icon{
        display: inline-block;
        position: relative;
        top:3px;
    }
    .action-icon{
        display: inline-block;
        cursor: pointer;
        position: relative;
        top:3px;
    }

    .deployBtn{
        margin:25px 10px;
    }
</style>



