import {repoApiGet} from '@/assets/js/baseApi'
import {getRepoServerApi} from "../restfulapi/serverConfigApi"

let getRepoLinuxPkgsApi = function(){
    return new Promise((resolve, reject) => {
        getRepoServerApi().then((repoUrl) => {
            let getRepoLinuxPkgUrl =  repoUrl + "/repoLinuxPkg";
            repoApiGet(getRepoLinuxPkgUrl, "").then((data) => {
                resolve(data);
            }).catch((error) => {
                resolve(error.response);
            })
        })
    })
}

export {
    getRepoLinuxPkgsApi,
}


