<template>
    <div>
        <el-tabs v-model="activeName">
            <el-tab-pane name="compose">
                <span slot="label">
                    <i class="fa fa-th-list m-r-5"></i>
                    {{$t('workorder.compose')}}
                </span>
                <el-table
                    :data="composeData"
                    tooltip-effect="dark"
                    style="width: 100%"
                    v-loading="listLoading">

                    <el-table-column
                    label=""
                    width="100"
                    >
                        <template slot-scope="scope">
                            <div>
                                <img src="@/assets/imgs/docker.png" alt="no img" style="width:48px"/>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="tag"
                    :label="$t('dockerControl.projectName')"
                    min-width="120"
                    sortable
                    >
                    </el-table-column>
                    <el-table-column
                    prop="version"
                    :label="$t('dockerControl.version')"
                    min-width="120"
                    sortable>
                    </el-table-column>
                    <el-table-column
                    prop="type"
                    :label="$t('dockerControl.type')"
                    min-width="120"
                    sortable>
                    </el-table-column>
                    <el-table-column
                        label="View"
                        min-width="100">
                        <template slot-scope="scope"> 
                            <div>
                                <i class="fa fa-eye pointer c-primary" aria-hidden="true" @click="showYmlContent(scope.row)" v-if="scope.row.type != 'uninstall'"></i>
                                <i class="fa fa-eye pointer c-darkgray" aria-hidden="true"  v-if="scope.row.type === 'uninstall'&& scope.row.control == 'Limited'"></i>
                                <i class="fa fa-eye pointer c-primary" aria-hidden="true" @click="showDeviceYmlContent(scope.row)" v-if="scope.row.type === 'uninstall'&& scope.row.control != 'Limited'"></i>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                        :label="$t('dockerControl.status')"
                        min-width="100">
                        <template slot-scope="scope"> 
                            <div>
                                <i class="fa fa-list-ul pointer c-primary" aria-hidden="true" @click="showDockerList(scope.row.repoDockers)"></i>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    :label="$t('global.action')"
                    min-width="120"
                    >
                        <template slot-scope="scope">
                            <div class="action-icon">
                                <svg t="1572233483828" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" 
                                    p-id="10629" width="20" height="20" 
                                    v-if="!scope.row.isSave&& scope.row.type=='Swarm'" 
                                    @click="addFile(scope.row, 'installDockerSwarm')">
                                <path d="M831.55 128.531c38.35 0 63.911 25.568 63.911 63.91v639.104c0 38.355-25.561 63.916-63.91 63.916H192.44c-38.34 0-63.908-25.56-63.908-63.916V192.442c0-38.343 25.567-63.91 63.908-63.91h639.11m0-63.91H192.44c-70.3 0-127.816 57.518-127.816 127.82v639.103c0 70.308 57.515 127.833 127.816 127.833h639.11c70.294 0 127.822-57.525 127.822-127.833V192.442c0-70.302-57.527-127.82-127.823-127.82z m0 0" p-id="10630" fill="#aaaaaa"></path>
                                </svg>
                                <svg t="1572233483828" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" 
                                    p-id="10629" width="20" height="20" 
                                    v-else-if="!scope.row.isSave&& scope.row.type=='Compose'" 
                                    @click="addFile(scope.row, 'installDockerCompose')">
                                <path d="M831.55 128.531c38.35 0 63.911 25.568 63.911 63.91v639.104c0 38.355-25.561 63.916-63.91 63.916H192.44c-38.34 0-63.908-25.56-63.908-63.916V192.442c0-38.343 25.567-63.91 63.908-63.91h639.11m0-63.91H192.44c-70.3 0-127.816 57.518-127.816 127.82v639.103c0 70.308 57.515 127.833 127.816 127.833h639.11c70.294 0 127.822-57.525 127.822-127.833V192.442c0-70.302-57.527-127.82-127.823-127.82z m0 0" p-id="10630" fill="#aaaaaa"></path>
                                </svg>
                                <svg t="1572233566165" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
                                    p-id="10804" width="20" height="20"
                                    v-else
                                    @click="deleteFile(scope.row.wiid, 'installDockerCompose')">
                                    <path d="M744.512 392.768L453.825 681.984l-41.92-41.664 290.689-289.28 41.92 41.728z m-423.168 73.984l132.481 131.84-41.92 41.728-132.481-131.904 41.92-41.664z m527.872 479.872H174.785c-62.016 0-112.384-48.704-112.384-108.671V186.05c0-59.968 50.432-108.671 112.384-108.671h674.431c62.016 0 112.384 48.704 112.384 108.671v651.903c0 59.968-50.368 108.671-112.384 108.671z m56.192-760.576c0-29.952-25.217-54.336-56.193-54.336H174.784c-30.976 0-56.193 24.384-56.193 54.336v651.903c0 29.952 25.217 54.336 56.193 54.336h674.431c30.976 0 56.193-24.384 56.193-54.336V186.048z" p-id="10805" fill="#3c8dbc"></path>
                                </svg>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="deployBtn" v-show="composeBtnIsShow">
                    <el-button 
                    size="small" 
                    type="default" 
                    @click="back()" 
                    >
                        {{$t('global.back')}}
                    </el-button>
                    <el-button 
                    size="small" 
                    type="primary" 
                    @click="goSummary()" 
                    >
                        {{$t('global.summary')}}
                    </el-button>
                </div>
            </el-tab-pane>
            <el-tab-pane name="linuxpkg">
                <span slot="label">
                    <i class="fa fa-linux m-r-5" ></i>
                    {{$t('workorder.linuxPkg')}}
                </span>
                <el-table
                    :data="linuxPkgData"
                    tooltip-effect="dark"
                    style="width: 100%"
                    v-loading="listLoading"
                    :header-cell-style="{background:'#fafafa',color:'#606266'}"
                    >

                    <el-table-column
                    label=""
                    width="100"
                    >
                        <template slot-scope="scope">
                            <div>
                                <img src="@/assets/imgs/deb.png"  alt="no img" style="width:52px" v-if="scope.row.type=='deb'"/>
                                <img src="@/assets/imgs/package.png"  alt="no img" style="width:48px" v-if="scope.row.type=='tar'"/>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="filename"
                    :label="$t('linuxPkgControl.fileName')"
                    min-width="120"
                    >
                    </el-table-column>
                    <el-table-column
                    prop="productname"
                    :label="$t('linuxPkgControl.productName')"
                    min-width="120"
                    sortable
                    >
                    </el-table-column>

                    <el-table-column
                    prop="version"
                    :label="$t('linuxPkgControl.version')"
                    min-width="120"
                    sortable>
                    </el-table-column>
                    <el-table-column
                    prop="type"
                    :label="$t('linuxPkgControl.fileType')"
                    min-width="120"
                    :filters="[{ text: $t('linuxPkgControl.debPkg'), value: 'deb' }, { text: $t('linuxPkgControl.compressPkg'), value: 'tar' }]"
                    :filter-method="filterFileType"
                    filter-placement="bottom-end"
                    >
                        <template slot-scope="scope">
                            <div>
                                <span v-if="scope.row.type == 'deb'">
                                    {{$t('linuxPkgControl.debPkg')}}
                                </span>
                                <span v-if="scope.row.type == 'tar'">
                                    {{$t('linuxPkgControl.compressPkg')}}
                                </span>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    :label="$t('global.action')"
                    min-width="120"
                    >
                        <template slot-scope="scope">
                            <div class="action-icon">
                                <svg t="1572233483828" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" 
                                    p-id="10629" width="20" height="20" 
                                    v-if="!scope.row.isSave" 
                                    @click="addFile(scope.row, 'installLinuxPkg')">
                                <path d="M831.55 128.531c38.35 0 63.911 25.568 63.911 63.91v639.104c0 38.355-25.561 63.916-63.91 63.916H192.44c-38.34 0-63.908-25.56-63.908-63.916V192.442c0-38.343 25.567-63.91 63.908-63.91h639.11m0-63.91H192.44c-70.3 0-127.816 57.518-127.816 127.82v639.103c0 70.308 57.515 127.833 127.816 127.833h639.11c70.294 0 127.822-57.525 127.822-127.833V192.442c0-70.302-57.527-127.82-127.823-127.82z m0 0" p-id="10630" fill="#aaaaaa"></path>
                                </svg>
                                <svg t="1572233566165" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
                                    p-id="10804" width="20" height="20"
                                    v-else
                                    @click="deleteFile(scope.row.wiid, 'installLinuxPkg')">
                                    <path d="M744.512 392.768L453.825 681.984l-41.92-41.664 290.689-289.28 41.92 41.728z m-423.168 73.984l132.481 131.84-41.92 41.728-132.481-131.904 41.92-41.664z m527.872 479.872H174.785c-62.016 0-112.384-48.704-112.384-108.671V186.05c0-59.968 50.432-108.671 112.384-108.671h674.431c62.016 0 112.384 48.704 112.384 108.671v651.903c0 59.968-50.368 108.671-112.384 108.671z m56.192-760.576c0-29.952-25.217-54.336-56.193-54.336H174.784c-30.976 0-56.193 24.384-56.193 54.336v651.903c0 29.952 25.217 54.336 56.193 54.336h674.431c30.976 0 56.193-24.384 56.193-54.336V186.048z" p-id="10805" fill="#3c8dbc"></path>
                                </svg>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="deployBtn"  v-show="pkgBtnIsShow">
                    <el-button 
                    size="small" 
                    type="default" 
                    @click="back()" 
                    >
                        {{$t('global.back')}}
                    </el-button>
                    <el-button 
                    size="small" 
                    type="primary" 
                    @click="goSummary()" 
                    >
                        {{$t('global.summary')}}
                    </el-button>
                </div>
            </el-tab-pane>
        </el-tabs> 
        <el-dialog  :visible.sync="dialogDockerVisible">
            <div slot="title" class="dialog-title">
                <i class="fa fa-list-ul m-r-10" aria-hidden="true"></i>
                {{$t('dockerControl.status')}}
            </div>
            <el-table :data="dockerData">
                <el-table-column property="container" label="container"></el-table-column>
                <el-table-column property="image" label="image"></el-table-column>
                <el-table-column property="version" label="version"></el-table-column>
                <el-table-column property="isRunning" label="Status" align="center">
                    <template slot-scope="scope">
                        <span v-if="scope.row.isRunning" :class="{'c-success': scope.row.isRunning=='running'}">{{scope.row.isRunning}}</span>
                        <span v-else>No Ready</span>
                    </template>
                </el-table-column>
            </el-table>
        </el-dialog>
        <el-dialog  :visible.sync="dialogYamlVisible">
            <div slot="title" class="dialog-title">
                <i class="fa fa-file-text m-r-10" aria-hidden="true"></i>
                {{$t('dockerControl.fileContent')}}
            </div>
            <div class="text-center">
                <editor v-model="content" @init="editorInit" lang="html" theme="chrome"  height="550"></editor>
            </div>
            
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogYamlVisible = false" size="small">{{$t('global.cancel')}}</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
    import {workorder} from '@/assets/js/lwm2mMap';
    import handleResponse from '@/components/restfulapi/handleResponse';
    import {getRepoDockerApi, getYmlContentApi} from '@/components/restfulapi/getRepoDockerApi';
    import {getRepoLinuxPkgsApi} from "@/components/restfulapi/getRepoLinuxPackageApi";  
    
    import {
        getWorkorderApi,
        getWorkorderByNameApi,
        getWorkorderItemByNameApi,

        editWorkorderApi,
        deleteWorkorderApi,
        addWorkorderApi,
        addWorkorderItemApi,
        deleteWorkorderItemApi,

    } from '@/components/restfulapi/workorderApi';
    import {checkSession, getSession, setSession } from '@/assets/js/storage';

    export default{
        name: 'workerorder-add',
        data(){
            let validateDepName = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[\u4E00-\u9FA5A-Za-z0-9_\-\.\s]{1,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            return {
                activeName: "compose",
                composeData: [],
                linuxPkgData: [],
                dockerData: [],
                listLoading: false,
                content: "",
                dialogYamlVisible: false,
                dialogDockerVisible: false
            }
        },

        components: { 
            editor: require('vue2-ace-editor'),
        },

        props:{
            wdname: {
                type: String,
                default: ""
            }, 

            wdtype: {
                type: String,
                default: ""
            }, 
        },

        methods:{
            //batch selected
            selectItem(val) {
                this.multipleSelection = val;
            },
            //get repo composes
            
            getRepoDockerCompose(){
                this.listLoading = true;
                getRepoDockerApi().then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                this.dockerAssignWdItems(res.data);
                            }
                            
                        }
                    })
                })
            },
            dockerAssignWdItems(repofiles){
                getWorkorderItemByNameApi(this.wdname).then((data) => {
                    handleResponse(data, (res) => {
                        if(Object.prototype.toString.call(res) === "[object Array]"){
                            let fileData = [];
                            repofiles.forEach((fileInfo) => {
                                res.forEach((item) => {
                                    if(item.pkgname === fileInfo.tag&&
                                    item.versionname === fileInfo.version&&
                                    (item.target === workorder.installDockerCompose||
                                    item.target == workorder.installDockerSwarm)){
                                        fileInfo.isSave = true;
                                        fileInfo.wiid = item.wiid;
                                    }
                                })
                                fileData.push(fileInfo);
                            })
                            this.composeData = fileData;
                            // console.log(this.composeData);
                            
                        }
                    })
                })
            },


            //get repo files
            getRepoLinuxPkgs(){
                getRepoLinuxPkgsApi().then((data) => {
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                this.linuxPkgAssignWdItems(res.data);
                            }
                            
                        }
                    })
                })
            },
            linuxPkgAssignWdItems(repofiles){
                getWorkorderItemByNameApi(this.wdname).then((data) => {
                    handleResponse(data, (res) => {
                        if(Object.prototype.toString.call(res) === "[object Array]"){
                            let fileData = [];
                            repofiles.forEach((fileInfo) => {
                                res.forEach((item) => {
                                    if(item.pkgname === fileInfo.productname&&
                                    item.versionname === fileInfo.version&&
                                    item.target === workorder.installLinuxPkg){
                                        fileInfo.isSave = true;
                                        fileInfo.wiid = item.wiid;
                                    }
                                })
                                fileData.push(fileInfo);
                            })
                            this.linuxPkgData = fileData;
                        }
                    })
                })
            },

            addFile(fileinfo, type){
                let pkgname, versionname, address;
                if(type == "installDockerCompose"|| type == "installDockerSwarm"){
                    pkgname  = fileinfo.tag;
                    versionname = fileinfo.version;
                }else{
                    pkgname = fileinfo.productname;
                    versionname = fileinfo.version;
                }  
                address = fileinfo.address;
                this.listLoading = true;
                addWorkorderItemApi(this.wdname, fileinfo.filename, workorder[type], pkgname, versionname, address).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        if(res.status === "CHANGED"){
                            if(type === "installDockerCompose"|| type == "installDockerSwarm"){
                                this.getRepoDockerCompose();    
                            }else{
                                this.getRepoLinuxPkgs();
                            }
                                
                        }else{
                            _g.handleError(res);
                        }
                    })
                    
                })
            },

            
            deleteFile(wiid, type) {
                this.listLoading = true;
                deleteWorkorderItemApi(wiid).then((res) => {
                    this.listLoading = false;
                    handleResponse(res, (data) => {
                        if(data.status === "CHANGED"){
                            if(type === "installDockerCompose"|| type == "installDockerSwarm"){
                                this.getRepoDockerCompose();    
                            }else{
                                this.getRepoLinuxPkgs();
                            }
                        }else{
                            _g.handleError(data);
                        }
                    })
                })
            },
            

            initData(){
                this.composeData = [];
                this.linuxPkgData = [];
            },

            goSummary(){
                this.$router.push({path: "/main/workorder/summary", query: { wdname: this.wdname, type: this.wdtype}})
            },
            //docker compose 
            editorInit: function () {
                require('brace/ext/language_tools') //language extension prerequsite...
                require('brace/mode/html')                
                require('brace/mode/javascript')    //language
                require('brace/mode/less')
                require('brace/theme/twilight')
                require('brace/snippets/javascript') //snippet
            },

            showYmlContent(info){
                this.address = info.address;
                this.dialogYamlVisible = true;
                getYmlContentApi(this.address).then((res) => {
                    handleResponse(res, (data) => {
                        if(data.status === "success"){
                            this.content = data.data;
                        }
                    })
                })
            },

            showDockerList(dockerData){
                this.dockerData = [];
                this.dockerData = dockerData;
                this.dialogDockerVisible = true;
            },

            //linux pkg
            filterFileType(value, row){
                return row.type === value;
            },

            back(){
                this.$router.go(-1);
            },
        },
        
        computed: {
            composeBtnIsShow(){
                if(this.composeData.length>0){
                    return true;
                }else{
                    return false;
                }
            },

            pkgBtnIsShow(){
                if(this.linuxPkgData.length>0){
                    return true;
                }else{
                    return false;
                }
            },
            dockerIconColor(){
                if(this.activeName == "compose"){
                    this.dockerIconColor = " #409EFF"
                }else{
                    this.dockerIconColor = " #303133"
                }
            }
           
        },

        created() {
            this.getRepoDockerCompose();
            this.getRepoLinuxPkgs();
        },

    }
</script>
<style lang="scss" scoped>
@import "../../../assets/css/colors";
    .wd-icon{
        display: inline-block;
        position: relative;
        top:3px;
    }
    .action-icon{
        display: inline-block;
        cursor: pointer;
        position: relative;
        top:3px;
    }

    .deployBtn{
        margin:25px 10px;
    }
</style>



